.greetingForm {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.inputAndButtonContainer {
  display: flex;
  gap: 10px;

}

.error {
  color: red;
  font-size: 14px;
}

.inputAndButtonContainer input {
  box-sizing: border-box;
  width: 370px;
  height: 36px;
  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  padding: 0 10px;
  font-size: 16px;
}


.input:focus {
}

.inputAndButtonContainer .errorInput {
  border: 1px solid red;
}

.button {
  /* height: 36px; */
  background-color: rgba(0, 102, 204, 1);
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  padding: 5px 25px;
  border-radius: 3px;
  border: 0;
  max-height: 36px ;
}

.button:focus {
}

.button:active {
  background-color: rgb(66, 132, 198);
}

.button:disabled {
  background-color: rgba(0, 102, 204, 1);
  opacity: 50%;
}

.text {
  color: rgba(0, 0, 0, 0.5);
  font-size: 14px;
}

.greeting {
  font-size: 14px;
  font-weight: 500;  
}
