.button {
  /* all: unset; */
  border-radius: 3px;
  width: 130px;
  height: 30px;
  padding: 5px 25px;
  color: rgba(255, 255, 255, 1);
  border: none;
  font-weight: 600;
  font-size: 14px;

}

.disabled {
  background-color: rgba(0, 77, 153, 1);
  opacity: 50%;
}

.secondary {
  width: 175px;
  height: 30px;
  border: 1px solid rgba(0, 102, 204, 1);
  background-color: rgba(255, 255, 255, 1);
  color: rgba(0, 102, 204, 1);
}

.default {
  background-color: rgba(0, 102, 204, 1);

}

.red {
  background-color: rgba(204, 20, 57, 1);
}

.default:hover {
  background-color: rgba(0, 128, 255, 1);
}

.red:hover {
  background-color: rgba(255, 26, 71, 1);
}

.secondary:hover {
  background-color: rgb(229,240,250);
}


.default:active {
  background-color: rgba(0, 89, 178, 1);
}

.secondary:active {
  background-color: rgb(201,229,255);
 
}

.red:active {
  background-color: rgba(178, 18, 50, 1);
}

.button:disabled {
}
